.modal {
  display: none;
  top: 10px;
  position: absolute;
  width: 300px;
  border-radius: 10px;

  left: calc((100% - 300px) /2);

  box-shadow: 0 0 20px -5px black;
  background-color: #fff;
  padding: 20px;
  padding-bottom: 40px;
  box-sizing: border-box;
  z-index: 1;
}

.modal__fieldset {
  border: none;
  margin-bottom: 20px;
}

.modal__field {
  display: block;
  margin-bottom: 10px;
}

.modal__label {
  text-align: center;
  display: block;
  width: 100%;
  margin-bottom: 10px;
  font-family: "Open Sans", Arial, sans-serif;
  color: #353340;
  font-weight: 800;
}

.modal__input{
  width: 100%;
  box-sizing: border-box;
  padding: 10px;
  border-radius: 10px;
  border: 1px solid #dedde2;

  &::placeholder {
    color: rgba(#353340, 0.2);
  }
}

.modal__btn {
  display: block;
  margin: 0 auto;
  padding: 15px 20px;
  background-color: tomato;
  border: none;
  border-radius: 10px;
  color: white;
  font-weight: 800;
}

.modal__close {
  background-color: transparent;
  border: none;
}

.modal--show  {
  display: block;
}


@media (min-width: 1120px) {
  .modal {
    top: 80px;
  }
}