.page-content__wrapper {
  padding-top: 20px;
}
.page-content__title {
  font-size: 36px;
  line-height: 1.5;
  margin-top: 0;
}

.page-content__item {
  margin-bottom: 30px;

  &:last-of-type {
    margin-bottom: 0;
  }
}

.page-content__link {
  font-weight: 800;
  font-size: 16px;
  color: tomato;
}

.page-content__sub-menu {
  margin-top: 10px;
}
.page-content__sub-item {
  margin-bottom: 30px;

  &:last-of-type {
    margin-bottom: 0;
  }
}

.page-content__sub-link {
  display: block;
  margin-bottom: 15px;

  text-decoration: underline;

  &:last-of-type {
    margin-bottom: 0;
  }

  &--git {
    font-weight: 800;
    font-size: 14px;
    color: #353340;
  }

  &--layout {
    font-weight: 800;
    font-size: 14px;
    color: #d9b779;
  }
}

@media (min-width: 1120px) {
  .page-content__menu {
    display: flex;
    flex-wrap: wrap;
  }

  .page-content__item {
    margin-right: 40px;
    margin-bottom: 50px;

    &--projects {
      width: 100%;
      margin-right: 0;
    }
  }


  .page-content__sub-menu {
    display: flex;
    flex-wrap: wrap;
  }

  .page-content__sub-item {
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    width: (980px / 3);
  }

  .page-content__sub-link {
    margin: 0;
    margin-right: 20px;
    &--git {
      width: 100%;
      margin-bottom: 20px;
    }
  }

  .page-content__link,
  .page-content__sub-link{
    &:hover,
    &:focus {
      opacity: 0.8;
    }

    &:active {
      opacity: 0.6;
    }

    &:not([href]) {
      opacity: 1;
    }
  }
}
